@import "../_baby-variables.less";
@import "../_baby-mixins.less";

.fn-@{cms-ns}-activity-category{
	.activity{
		background: #fff;

		&:before {
			padding-top: 32%;
		}

		.btn{
			background: @ht-color;
			color: #fff;
			.absolute(_,10px,10px,_);
			padding: 5px 10px;

			i{
				line-height: 1;
				font-size: 20px;
				margin-right: 2px;
				vertical-align: top;
			}
		}

		.ico{
			.absolute(_,_,0,50%);
			.size(0);
			border-color: transparent transparent #fff transparent;
			border-style: dashed dashed solid dashed;
			border-width: 6px;
			margin-left: -6px;
		}
	}

	.category{
		padding: 10px;
		background: #fff;

		.item{
			text-align: center;
			padding: 0 5px;
		}
	}
}
