@import "../_baby-variables.less";
@import "../_baby-mixins.less";

@import "../../_plugin/cmp-level-flex.less";

@import "../incl/baby-pic-grid.less";


.fn-@{cms-ns}-label-list{
	.label-list{
		padding: 10px 5px;
		text-align: center;

		.item{
			padding: 5px;

			&.active .label{
				color: @baby-color;
			}
		}

		.label{
			background: #fff;
			display: block;
			padding: 5px 0;
			.ellipsis();
			border-radius: 10px;
			border:1px solid @baby-color;
			color: @baby-color;
		}
	}
	.pic-grid{
		padding: 0 10px;
		.baby-pic-grid();
	}

	.item-list{
		background: @body-background;
		
		.mark{
			.absolute(-2px,_,_,10px);
			background: data-uri('@{cms-image-dir}/@{baby-ns}/baby_recommon_mark.png') no-repeat 0 0;
			background-size: 100% 100%;
			z-index:2;
			.size(32px,27.5px);
			color:#fff;
			text-align: center;
			line-height: 26px;
			overflow: hidden;
			font-size: @font-size-sm;
			font-style: normal;
			text-align: center;
		} 
		
		.cmp-level-list();

		.detail{
			padding-top: 20px;
		}
	}

}
